.header {
  background-color: #fff;
  padding: 0px 0px 5px 0px;
  margin-bottom: 18px;
  height: auto;
  line-height: 30px;
}
.header .header-top {
  font-size: 1rem;
  padding-right: 30px;
  text-align: right;
}
.header .header-top .logout {
  color: #fff;
  border-radius: 20px;
  height: 1.4rem;
  font-size: 0.3rem;
}
.header hr {
  opacity: 0.3;
}
.header .header-bottom {
  display: flex;
  align-items: center;
}
.header .header-bottom .header-bottom-left {
  width: 25%;
  text-align: center;
  font-size: large;
  position: relative;
}
.header .header-bottom .header-bottom-left::after {
  content: '';
  position: absolute;
  right: 50%;
  top: 100%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  border-top: 20px solid #fff;
  border-right: 20px solid transparent;
  border-bottom: 20px solid transparent;
  border-left: 20px solid transparent;
}
.header .header-bottom .header-bottom-right {
  width: 75%;
  padding-right: 30px;
  text-align: right;
}
.header .header-bottom .header-bottom-right img {
  margin: 0px 10px;
  padding-bottom: 4px;
  width: 20px;
  height: 20px;
}
